import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {
  NativeSettingsService
} from '@nequi/apps/base/mobile/config/features/commons/services/native-settings';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { DocumentTypes } from '@nequi/features/commons/core';
import { AlertService, FeatureBasePage } from '@nequi/features/commons/presentation';
import {
  SessionProvider,
  TranslateProvider,
  UserInfoProvider
} from '@nequi/features/commons/providers';
import { ProfileConfig } from '@nequi/features/profile/config';
import { ProfileModel } from '@nequi/features/profile/data';
import { ProfileService } from '@nequi/features/profile/presentation';
import { PinChallengeModalService } from '@nequi/features/security/auth/presentation';
import { TypeBiometric } from '@nequi/features/security/biometry/core';
import { BiometryType } from '@nequi/features/security/biometry/core/constants';
import { FingerPrintPlugin } from '@nequi/native/finger-print';
import { CardModalService, LoadingService, ToastService } from '@nequi/ui';
import { ModalButton } from '@nequi/ui/organisms/modals';

import { FingerprintError } from '../entities/fingerprint-error';
import { ProfileSecurityConfig } from './profile-security-config';

@Component({
  selector: 'app-profile-security',
  templateUrl: './profile-security.page.html',
  styleUrls: ['./profile-security.page.scss'],
})
export class ProfileSecurityPage extends FeatureBasePage implements OnInit {
  public config = ProfileSecurityConfig;
  public isFingerprintAvailable: boolean;
  public isPasswordOnTransactions: boolean;
  public isFingerPrintEnable: boolean;

  public get fingerprintStatus(): TypeBiometric {
    return this.profileService.getFingerPrintBiometry();
  }

  public get voiceBiometryStatus(): TypeBiometric {
    return this.profileService.getVoiceBiometry();
  }

  public get isPasswordOnTransactionsActivated() {
    return this.profileModel?.security?.value || false;
  }

  public get dataTreatmentUrl(): string {
    return this.config.routes.dataTreatment;
  }

  private messages: any;

  constructor(
    injector: Injector,
    private userInfoProvider: UserInfoProvider,
    private translateProvider: TranslateProvider,
    private alertService: AlertService,
    private profileModel: ProfileModel,
    private fingerPrintPlugin: FingerPrintPlugin,
    private route: ActivatedRoute,
    private profileService: ProfileService,
    private loadingService: LoadingService,
    private toastService: ToastService,
    private cardModalService: CardModalService,
    private nativeSettingsService: NativeSettingsService,
    private pinChallengeModalService: PinChallengeModalService,
    private applicationDataInteractor: ApplicationDataInteractor,
    private sessionProvider: SessionProvider
  ) {
    super(injector);
    this.messages = this.translateProvider.get(this.config.i18n.passOnTransactions);
    this.isPasswordOnTransactions = this.isPasswordOnTransactionsActivated;
    this.isFingerPrintEnable = this.fingerprintStatus.enabled;
  }

  public async ngOnInit() {
    await this.validateIsFingerprintAvailable();
  }

  public goBack() {
    this.navigatorProvider.navigateBack(this.config.routes.settings);
  }

  public ionViewDidEnter() {
    this.manageFingerprintToggle();
  }

  public async togglePasswordOnTransactions() {
    if (this.sessionProvider.isContingencyAppGate) {
      this.revertPasswordStatus();

      return this.showMessageContingencyAppGate();
    }

    if (this.isPasswordOnTransactions) {
      await this.alertService.showByI18n({
        i18nKey: this.config.i18n.deactivatePassword,
        handlers: {
          accept: () => this.updateUserProfilePasswordOnTransactions(false),
          cancel: () => this.revertPasswordStatus(),
        },
      });

      return;
    }

    await this.alertService.showByI18n({
      i18nKey: this.config.i18n.activatePassword,
      handlers: {
        accept: () => this.updateUserProfilePasswordOnTransactions(true),
        cancel: () => this.revertPasswordStatus(),
      },
    });
  }

  public async setVoiceBiometry(event: CustomEvent) {
    event.stopImmediatePropagation();

    try {
      if (!this.voiceBiometryStatus.enrolled) {
        this.showBiometryFacialModalSettings();

        return;
      }

      if (this.voiceBiometryStatus.enabled && this.voiceBiometryStatus.enrolled) {
        this.showUnregisterAlert();

        return;
      }

      this.updateVoiceBiometryOnLoginConfiguration();
    } catch (error) {
      this.showBiometryFacialModalSettings();
    }
  }

  public async handlerUnregisterAlert() {
    if (await this.pinChallengeIsValid()) {
      this.updateVoiceBiometryOnLoginConfiguration();
    }
  }

  public async showUnregisterAlert() {
    const messages = this.translateProvider.get(this.config.i18n.unregisterVoiceBiometrySettings);

    this.alertService.show({
      messages,
      handlers: {
        accept: () => this.handlerUnregisterAlert(),
      },
    });
  }

  public pinChallengeIsValid(): Promise<boolean> {
    const pinChallengeMessage = this.translateProvider.get(this.config.i18n.pinChallengeMessage);

    return this.pinChallengeModalService.show(pinChallengeMessage);
  }

  public async toggleFingerprintOnLogin() {
    if (this.sessionProvider.isContingencyAppGate) {
      this.isFingerPrintEnable = this.fingerprintStatus.enabled;

      return this.showMessageContingencyAppGate();
    }

    try {
      if (!this.fingerPrintPlugin.isAvailable()) {
        return;
      }

      if (this.fingerprintStatus.enabled) {
        this.updateFingerprintOnLoginConfiguration();

        return;
      }

      this.navigatorProvider.navigateForward([this.config.routes.fingerprint], {
        relativeTo: this.route,
      });
    } catch (error) {
      this.openFingerprintModalSettings(error);
    }
  }

  public async goToDataTreatment() {
    const isDocumentTI: boolean = this.userInfoProvider.getDocument().type === DocumentTypes.TI;

    if (isDocumentTI) {
      const acceptHandler = async () => {
        this.navigatorProvider.navigateRoot(this.config.routes.backpack, {
          relativeTo: this.route,
        });
      };

      await this.showCardModal(this.config.i18n.dataTreatmentMinorsModalSettings, acceptHandler);

      return;
    }

    this.navigatorProvider.navigateForward(this.dataTreatmentUrl);
  }

  private async manageFingerprintToggle() {
    const isTermsAndConditionsAccepted = await this.applicationDataInteractor.findValue<boolean>(
      ProfileConfig.applicationData.isTermsAndConditionsAccepted,
      this.fingerprintStatus.enabled
    );

    if (isTermsAndConditionsAccepted) {
      this.isFingerPrintEnable = true;
    } else {
      this.isFingerPrintEnable = false;
    }
  }

  private revertPasswordStatus() {
    this.isPasswordOnTransactions = !this.isPasswordOnTransactions;
  }

  private async showBiometryFacialModalSettings() {
    const acceptHandler = async () => {
      this.navigatorProvider.navigateForward([this.config.routes.facialbiometry], {
        relativeTo: this.route,
      });
    };

    await this.showCardModal(this.config.i18n.biometryFacialModalSettings, acceptHandler);
  }

  private async updateUserProfilePasswordOnTransactions(isPasswordOnTransactions: boolean) {
    try {
      await this.loadingService.show();
      this.profileModel.flagBiometry = false;
      this.profileModel.flagChangeFingerPrint = false;
      this.profileModel.isUpdatePassOnTransactionsProcess = true;
      this.profileModel.security.value = isPasswordOnTransactions;

      await this.profileService.updateUserProfile();
      this.profileModel.isUpdatePassOnTransactionsProcess = false;

      await this.loadingService.hide(true);
      const message = isPasswordOnTransactions
        ? this.messages.successActivated
        : this.messages.successDeactivated;

      await this.toastService.showSuccess(message);
    } catch (error) {
      this.profileModel.isUpdatePassOnTransactionsProcess = false;
      this.profileModel.security.value = !isPasswordOnTransactions;
      await this.loadingService.hide(false);
      this.toastService.showError(error.message);
    }
  }

  private async updateVoiceBiometryOnLoginConfiguration() {
    const voicePreviousValue = JSON.parse(JSON.stringify(this.profileService.getVoiceBiometry()));
    const messages = this.translateProvider.get(this.config.i18n.unregisterVoiceBiometrySettings);

    try {
      await this.loadingService.show();
      this.profileService.prepareBiometryVoiceConfiguration(!voicePreviousValue.enabled);
      this.profileModel.flagBiometry = true;
      await this.profileService.updateUserProfile();
      await this.loadingService.hide(true);

      if (voicePreviousValue.enabled) {
        await this.toastService.showSuccess(messages.toast.disabled);

        return;
      }

      await this.toastService.showSuccess(messages.toast.enabled);
    } catch (error) {
      this.profileService.setBiometry(voicePreviousValue, BiometryType.VOICE);
      await this.loadingService.hide(false);
    }
  }

  private async validateIsFingerprintAvailable() {
    try {
      if (this.fingerPrintPlugin.isAvailable()) {
        this.isFingerprintAvailable = true;
      }
    } catch (error) {
      this.isFingerprintAvailable = false;
    }
  }

  private async updateFingerprintOnLoginConfiguration() {
    const fingerprintPreviusValue = JSON.parse(
      JSON.stringify(this.profileService.getFingerPrintBiometry())
    );

    try {
      await this.loadingService.show();
      this.profileService.prepareFingerPrintConfiguration(false);

      await this.profileService.updateUserProfile();
      await this.profileService.saveFingerprintOnLoginInLocalStorage(false);
      await this.loadingService.hide(true);
    } catch (error) {
      this.profileService.setBiometry(fingerprintPreviusValue, BiometryType.FINGERPRINT);
      await this.loadingService.hide(false);
    }
  }

  private async openFingerprintModalSettings(error: FingerprintError) {
    const messageObject = this.config.i18n[error];

    if (!messageObject) {
      return;
    }

    const acceptHandler = async () => {
      try {
        await this.nativeSettingsService.open(this.config.i18n.security);
      } catch (nativeError) {
        this.alertService.show(nativeError);
      }
    };

    await this.showCardModal(messageObject, acceptHandler);
  }

  private showMessageContingencyAppGate() {
    this.toastService.showInfo(this.translateProvider.get(this.config.i18n.contingencyAppGate));
  }

  private async showCardModal(
    i18nKey: string,
    acceptHandler?: () => void,
    cancelHandler?: () => void
  ) {
    const buttons: ModalButton[] = [];
    const message = this.translateProvider.get(i18nKey);

    buttons.push({
      text: message.buttons.accept,
      handler: acceptHandler ?? null,
    });

    if (message.buttons.cancel) {
      buttons.push({
        color: 'medianoche-gray',
        text: message.buttons.cancel,
        handler: cancelHandler ?? null,
      });
    }

    await this.cardModalService.show({
      title: message.title,
      content: message.content,
      imgPath: message.image,
      buttons,
    });
  }
}
