import { CoreException } from '@nequi/commons/exceptions';

export class SecurityPasswordViewModel {
  public isConfirming = false;
  public password = '';
  public error: CoreException;

  private initialPassword = '';

  public setInitialPassword(password: string) {
    this.initialPassword = password;
  }

  public isSamePassword(password: string): boolean {
    return this.initialPassword === password;
  }

  public setErrorMessage(message: string) {
    if(!message){
      return;
    }

    this.error = new CoreException(message);
  }
}
