import { Component, Injector } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LoggerInteractor, ProvisioningInteractor } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { AuthInteractor } from '@nequi/features/security/auth/core';
import { LoadingService } from '@nequi/ui';

import { ProfileSecurityConfig } from '../home/profile-security-config';

@Component({
  selector: 'app-facial-biometry',
  templateUrl: './facial-biometry.page.html',
  styleUrls: ['./facial-biometry.page.scss'],
})
export class FacialBiometryPage extends FeatureBasePage {
  public config = ProfileSecurityConfig;

  constructor(
    injector: Injector,
    private loadingService: LoadingService,
    private provisioningInteractor: ProvisioningInteractor,
    private loggerInteractor: LoggerInteractor,
    private authInteractor: AuthInteractor,
    private route: ActivatedRoute
  ) {
    super(injector);
  }

  public async successVerification() {
    try {
      const isDeviceProvisioned = await this.provisioningInteractor.isDeviceProvisioned();

      if (isDeviceProvisioned) {
        const softToken = await this.provisioningInteractor.getSoftToken();

        await this.authInteractor.validateSoftToken(softToken);
        await this.loadingService.hide(true);

        this.navigatorProvider.navigateRoot(this.config.routes.showBiometryByVoicePage);

        return;
      }
    } catch (error) {
      this.loggerInteractor.error(error);
    }
  }
}
