import { Component, Injector } from '@angular/core';
import { ApplicationDataInteractor } from '@nequi/features/application-data/core';
import { TranslateProvider } from '@nequi/features/commons';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { ProfileConfig } from '@nequi/features/profile/config';
import { ProfileService } from '@nequi/features/profile/presentation';
import { BiometryType } from '@nequi/features/security/biometry/core/constants';
import { LoadingService, ToastService } from '@nequi/ui';

import { SecurityFingerprintConfig } from './security-fingerprint.config';

@Component({
  selector: 'app-security-fingerprint',
  templateUrl: './security-fingerprint.page.html',
  styleUrls: ['./security-fingerprint.page.scss'],
})
export class SecurityFingerprintPage extends FeatureBasePage {
  constructor(
    injector: Injector,
    private profileService: ProfileService,
    private toastService: ToastService,
    private translateProvider: TranslateProvider,
    private applicationDataInteractor: ApplicationDataInteractor,
    private loadingService: LoadingService
  ) {
    super(injector);
  }

  public async acceptFingerPrintConfiguration() {
    const fingerprintPreviusValue = JSON.parse(
      JSON.stringify(this.profileService.getFingerPrintBiometry())
    );
    const toastMessages = this.translateProvider.get(SecurityFingerprintConfig.i18n.toastSuccess);

    try {
      await this.loadingService.show();
      this.profileService.prepareFingerPrintConfiguration(true);

      await this.profileService.updateUserProfile();

      await this.profileService.saveFingerprintOnLoginInLocalStorage(true);

      await this.toastService.showSuccess(toastMessages);
      this.saveTermsAndConditions(true);
      this.navigateBack(true);
    } catch (error) {
      this.profileService.setBiometry(fingerprintPreviusValue, BiometryType.FINGERPRINT);
      this.toastService.showError(error.message);
      this.navigateBack(false);
    }
  }

  public goBack() {
    this.saveTermsAndConditions(false);
    this.navigatorProvider.back();
  }

  private async navigateBack(hasErrors: boolean) {
    await this.loadingService.hide(hasErrors);
    this.navigatorProvider.back();
  }

  private saveTermsAndConditions(isTermsAndConditionsAccepted: boolean) {
    this.applicationDataInteractor.save(
      ProfileConfig.applicationData.isTermsAndConditionsAccepted,
      isTermsAndConditionsAccepted
    );
  }
}
