import { ChangeDetectorRef, Component, Injector } from '@angular/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { ProfileModel } from '@nequi/features/profile/data';
import { ProfileService } from '@nequi/features/profile/presentation';
import { LoadingService, ToastService } from '@nequi/ui';
import { passwordMeterCheck } from '@nequi/utils/functions';

import { SecurityPasswordViewModel } from './security-password.view-model';

const I18N_CHANGE_PASSWORD = 'profile-security.change-password';

@Component({
  selector: 'app-security-password',
  templateUrl: './security-password.page.html',
  styleUrls: ['./security-password.page.scss'],
})
export class SecurityPasswordPage extends FeatureBasePage {
  public viewModel = new SecurityPasswordViewModel();

  private messages: Record<string, any>;

  constructor(
    injector: Injector,
    private changeDetectorRef: ChangeDetectorRef,
    private profileModel: ProfileModel,
    private toastService: ToastService,
    private translateProvider: TranslateProvider,
    private profileService: ProfileService,
    private loadingService: LoadingService
  ) {
    super(injector);
  }

  public ionViewDidEnter() {
    this.messages = this.translateProvider.get(I18N_CHANGE_PASSWORD);
    this.toastService.showInfo(this.messages.toast.info);
  }

  public goBack() {
    if (this.viewModel.isConfirming) {
      this.viewModel.isConfirming = false;

      return;
    }

    this.navigatorProvider.back();
  }

  public keyPressed(password: string) {
    const responseMeter = passwordMeterCheck(password);

    if (responseMeter.isValid) {
      return;
    }

    this.resetAll();
    this.toastService.showError(this.messages.errors.repeated);
  }

  public onFilled() {
    if (this.viewModel.isConfirming) {
      this.processConfirmation();

      return;
    }

    this.initConfirmation();
  }

  private initConfirmation() {
    this.toastService.showSuccess(this.messages.toast.confirm);
    this.viewModel.isConfirming = true;
    this.viewModel.setErrorMessage(null);
    this.viewModel.setInitialPassword(this.viewModel.password);
    this.viewModel.password = '';

    this.changeDetectorRef.detectChanges();
  }

  private async processConfirmation() {
    if (!this.viewModel.isSamePassword(this.viewModel.password)) {
      this.resetAll();
      this.viewModel.setErrorMessage(this.messages.errors.different);

      return;
    }

    try {
      await this.loadingService.show();

      await this.profileService.changePassword({
        newPassword: this.viewModel.password,
        phoneNumber: this.profileModel.phoneNumber,
      });

      await this.loadingService.hide(true);
      await this.toastService.showSuccess(this.messages.toast.success);

      this.navigatorProvider.back();
    } catch (error) {
      this.loadingService.hide(false);
      this.resetAll();
      this.toastService.showError(error?.errorMessage || this.messages.errors.default);
    }
  }

  private resetAll() {
    this.viewModel.isConfirming = false;
    this.viewModel.password = '';
    this.viewModel.setInitialPassword('');
    this.changeDetectorRef.detectChanges();
  }
}
