/* eslint-disable @angular-eslint/component-selector */
import { Component, Injector } from '@angular/core';
import { AlertController } from '@ionic/angular';
import { DaonVoiceService } from '@nequi/apps/base/mobile/config/features/commons/services/daon';
import {
  NativeSettingsService
} from '@nequi/apps/base/mobile/config/features/commons/services/native-settings';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { ProfileModel } from '@nequi/features/profile/data';
import { ProfileService } from '@nequi/features/profile/presentation';
import { PermissionDeniedException } from '@nequi/features/security/biometry/core';
import { GoBackException } from '@nequi/native/commons';
import { CardModalService, LoadingService, ToastService } from '@nequi/ui';

import { ProfileSecurityConfig } from '../home/profile-security-config';
import { ShowBiometryByVoiceConfig } from './show-biometry-by-voice.config';

@Component({
  selector: 'nequi-biometry-by-voice',
  templateUrl: './show-biometry-by-voice.page.html',
  styleUrls: ['./show-biometry-by-voice.page.scss'],
})
export class ShowBiometryByVoicePage extends FeatureBasePage {
  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    public alertController: AlertController,
    private daonVoiceService: DaonVoiceService,
    private loadingService: LoadingService,
    private toastService: ToastService,
    private profileModel: ProfileModel,
    private profileService: ProfileService,
    private cardModalService: CardModalService,
    private nativeSettingsService: NativeSettingsService
  ) {
    super(injector);
  }

  public async captureVoice() {
    try {
      const voices = await this.daonVoiceService.getVoices();

      await this.loadingService.show();
      await this.daonVoiceService.registerVoice(voices);

      this.updateVoiceBiometryConfiguration();
      this.loadingService.hide();
    } catch (error) {
      if (error instanceof GoBackException) {
        return;
      }

      if (error instanceof PermissionDeniedException) {
        return this.showPermissionAlert();
      }

      this.loadingService.hide(false);
      this.toastService.showError(error.errorMessage);
    }
  }

  public async updateVoiceBiometryConfiguration() {
    const messages = this.translateProvider.get(
      ProfileSecurityConfig.i18n.unregisterVoiceBiometrySettings
    );

    this.profileModel.flagBiometry = true;
    this.profileService.prepareBiometryVoiceConfiguration(true);

    await this.profileService.updateUserProfile();
    this.navigatorProvider.navigateRoot(ShowBiometryByVoiceConfig.routes.security);
    await this.toastService.showSuccess(messages.toast.enabled);
  }

  public async goBack() {
    const messages = this.translateProvider.get(ShowBiometryByVoiceConfig.i18n.cancelAlert);

    const alert = await this.alertController.create({
      header: messages.header,
      cssClass: 'nequi-alert',
      message: messages.message,
      buttons: [
        {
          text: messages.buttons.accept,
          handler: () =>
            this.navigatorProvider.navigateRoot(ShowBiometryByVoiceConfig.routes.security),
        },
        {
          text: messages.buttons.cancel,
          role: 'cancel',
        },
      ],
    });

    await alert.present();
  }

  private async showPermissionAlert() {
    const messages = this.translateProvider.get(ShowBiometryByVoiceConfig.i18n.permissionsDenied);

    this.cardModalService.show({
      title: messages.title,
      imgPath: messages.image,
      content: messages.message,
      buttons: [
        {
          text: messages.buttons.accept,
          handler: () =>
            this.nativeSettingsService.open(ShowBiometryByVoiceConfig.applicationDetails),
        },
        {
          text: messages.buttons.cancel,
          color: 'medianoche-gray',
        },
      ],
    });
  }
}
