import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ModuleWithProvidersFactory, ORIGIN_FLOW, OriginFlow } from '@nequi/features/commons';

import { FacialBiometryPage } from './facial-biometry/facial-biometry.page';
import { ProfileSecurityPage } from './home/profile-security.page';
import { SecurityFingerprintPage } from './security-fingerprint/security-fingerprint.page';
import { SecurityPasswordPage } from './security-password/security-password.page';
import { ShowBiometryByVoicePage } from './show-biometry-by-voice/show-biometry-by-voice.page';

const routes: Routes = [
  {
    path: '',
    component: ProfileSecurityPage,
  },
  {
    path: 'password',
    component: SecurityPasswordPage,
  },
  {
    path: 'fingerprint',
    component: SecurityFingerprintPage,
  },
  {
    path: 'facial-biometry',
    component: FacialBiometryPage,
  },
  {
    path: 'show-biometry-by-voice',
    component: ShowBiometryByVoicePage,
  },
  {
    path: 'data-treatment',
    loadChildren: () =>
      import('@nequi/features/data-treatment/presentation').then(
        (m) =>
          new ModuleWithProvidersFactory(
            m.FeaturesDataTreatmentModule.withConfig([
              { provide: ORIGIN_FLOW, useValue: OriginFlow.PROFILE },
            ])
          )
      ),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ProfileSecurityRoutingModule {}
